<script lang="ts">
import { defineComponent, ref, computed } from "vue";
import { createAndAddContents } from "../api/picture_book";
import { useUserStore } from "../stores/user";

export default defineComponent({
  name: "PictureBookCreate",
  setup() {
    const userStore = useUserStore();

    const ki = ref("");
    const sho = ref("");
    const ten = ref("");
    const ketsu = ref("");

    const isFormValid = computed(() => {
      return (
        ki.value.trim() !== "" &&
        sho.value.trim() !== "" &&
        ten.value.trim() !== "" &&
        ketsu.value.trim() !== ""
      );
    });

    const isLoading = ref(false);
    const showCompletionPopup = ref(false);

    const createPictureBook = async () => {
      if (userStore.uid) {
        isLoading.value = true;
        const textData = `${ki.value} ${sho.value} ${ten.value} ${ketsu.value}`;

        try {
          await createAndAddContents(userStore.uid, textData);
          isLoading.value = false;
          showCompletionPopup.value = true;
        } catch (error) {
          console.error("Error creating picture book:", error);
          isLoading.value = false;
          alert(
            "ãˆã»ã‚“ã®ä½œæˆä¸­ã«ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸã€‚ã‚‚ã†ä¸€åº¦ãŠè©¦ã—ãã ã•ã„ã€‚"
          );
        }
      } else {
        console.error("User is not logged in");
        userStore.clearUser();
      }
    };

    const closeCompletionPopup = () => {
      showCompletionPopup.value = false;
    };

    return {
      ki,
      sho,
      ten,
      ketsu,
      isFormValid,
      isLoading,
      showCompletionPopup,
      createPictureBook,
      closeCompletionPopup,
    };
  },
});
</script>

